<template>
  <div>
    <!-- <div class="jumbotron">
      <div class="container">
        <div id="title">
          <h1>
            <strong>Q & A</strong>
          </h1>
          <hr />
          <p>문의 사항 페이지</p>
        </div>
      </div>
    </div> -->
    <div class="container">
      <table class="table">
        <thead>
          <tr>
            <th>#</th>
            <th>Title</th>
            <th>Writer</th>
            <th>Written date</th>
          </tr>
        </thead>
        <tr v-if="(targetPost)" :key="targetPost.qnaNum">
          <td>{{targetPost.qnaNum}}</td>
          <td>
            <input type="text" v-model="targetPost.title" />
          </td>
          <td>{{targetPost.writer}}</td>
          <td>{{targetPost.writtenDate}}</td>
        </tr>
        <tr>
          <td colspan="4">
            <textarea style="width:100%" v-model="targetPost.contents"></textarea>
          </td>
        </tr>
      </table>
      <br />
      <div align="right">
        <button
          type="button"
          class="btn btn-primary"
          id="modifyNoticeBtn"
          @click="editPost(targetPost.qnaNum)"
        >수정</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../assets/axios.js";
import router from "../assets/router";
export default {
  props: ["id"],
  data() {
    return {
      targetPost: {},
    };
  },
  mounted() {
    axios.get("/QnADetail/" + this.id)
      .then(res => {
        this.targetPost = res.data;
      })
  },
  methods: {
    editPost() {
      axios.put("/updateQnA", this.targetPost)
        .then(res => {
          if(res.data.state=='SUCCESS') {
            alert('수정 완료');
            router.push({
              path: "/QnADetail/" + this.id
            });
          } else {
            alert('수정 실패');
          }
        }).catch(e => {
          alert('수정 실패 : ' + e);
        })
    }
  }
};
</script>

