<template>
  <div class="container">
    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th>Title</th>
          <th>Writer</th>
          <th>Written date</th>
        </tr>
      </thead>
      <tbody>
        <tr v-if="qna">
          <td>{{qna.qnaNum}}</td>
          <td>{{qna.title}}</td>
          <td>{{qna.writer}}</td>
          <td>{{qna.writtenDate}}</td>
        </tr>
        <tr>
          <td colspan="4">{{qna.contents}}</td>
        </tr>
      </tbody>
    </table>
      <div align="right">
        <button type="button" class="btn btn-success" id="modifyNoticeBtn" @click="backToList()">목록</button>
        <button type="button" class="btn btn-primary" id="modifyNoticeBtn" @click="editPost(qna.qnaNum)">수정</button>
        <button type="button" class="btn btn-danger" id="deleteBtn" @click="deletePost(qna.qnaNum)" >삭제</button>
      </div>
  </div>
</template>

<script>
import axios from "../assets/axios.js";
import router from '../assets/router.js';
export default {
  props: ["qnaNum"],
  data() {
    return {
      qna: {}
    };
  },
  mounted() {
    axios
      .get("/QnADetail/" + this.qnaNum)
      .then(res => {
        this.qna = res.data;
      })
      .catch(e => {
        alert("error : " + e);
      });
  },
  methods: {
    deletePost(qnaNum) {
        axios.delete("/deleteQnA/" + qnaNum)
        .then(res => {
          this.targetPost = res.data;
          router.push({
            path: "/QnAMain"
          });
        })
        .catch(e => {
          console.log(e);
        })
        .finally(() => {});
    },
    editPost(qnaNum) {
      router.push({
        path: "/updateQnA/" + qnaNum
      });
    },
    backToList() {
      router.push({
        path: "/QnAMain"
      });
    }
  }
};
</script>

