<template>
	<div class="container">
		<form>
            <label for="writer" class="sr-only">작성자</label><input type="hidden" value="ssafy" v-model="qna.writer" id="writer" class="form-control"><br/>
			<label for="titleText">제목</label><input v-model="qna.title" id="titleText" class="form-control" type="text"><br/>
			<label for="contents">내용</label><textarea v-model="qna.contents" id="contents" class="form-control" rows="5"></textarea>
			<br />
			<div align="center">
				<button type="button" class="btn btn-primary" @click="regist">등록</button>
			</div>
		</form>
	</div>
</template>

<script>
import router from '../assets/router';
import axios from '../assets/axios';
export default {
    data() {
        return {
            qna: {writer: 'ssafy'}
        }
    },
    methods: {
        regist() {
            axios.post('/addQnA', this.qna)
            .then(res => {
                if(res.data.state=='SUCCESS') {
                    alert('QnA 등록 성공');
                    router.push('/QnAMain');
                } else {
                    alert('QnA 등록 실패');
                }
            }).catch(e => {
                alert('QnA 등록 실패 : ' + e);
            })
        }
    },
}
</script>

