import VueRouter from 'vue-router';
import QnAMain from '../components/qnaMain.vue';
import QnADetail from '../components/qnaDetail.vue';
import newQnA from '../components/newQnA.vue';
import updateQnA from '../components/QnADetailEdit.vue';
const router = new VueRouter({
    routes: [
        {
            name: 'QnAMain',
            path: '/QnAMain',
            component: QnAMain
        },
        {
            name: 'QnADetail',
            path: '/QnADetail/:qnaNum',
            component: QnADetail,
            props: true
        },
        {
            name: 'newQnA',
            path: '/newQnA',
            component: newQnA
        },
        {
            name:"updateQnA",
            path:"/updateQnA/:id",
            component: updateQnA,
            props: true
        }
    ]
})

export default router;