<template>
  <div class="container">
    <div>
      <form class="form-inline">
        <div class="form-group">
          <select class="form-control" id="searchOption" v-model="searchOption">
            <option value selected disabled hidden>Choose here</option>
            <option value="1">제목</option>
            <option value="2">작성자</option>
          </select>
        </div>
        <div class="form-group">
          <input type="text" class="form-control" id="searchText" v-model="searchTxt" />
        </div>
        <button type="button" class="btn btn-primary" @click="search()">검색</button>
      </form>
    </div>
    <table class="table table-hover">
      <thead>
        <tr>
          <th>#</th>
          <th>Title</th>
          <th>Writer</th>
          <th>Written date</th>
        </tr>
      </thead>
      <tbody>
        <tr @click="showDetail(qna.qnaNum)" v-for="qna in qnas" :key="qna.qnaNum">
          <td>{{qna.qnaNum}}</td>
          <td>{{qna.title}}</td>
          <td>{{qna.writer}}</td>
          <td>{{qna.writtenDate}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "../assets/axios";
import router from "../assets/router";
export default {
  data() {
    return {
      qnas: [],
      searchOption: 0,
      searchTxt: ""
    };
  },
  mounted() {
    axios
      .get("/QnAMain")
      .then(res => {
        this.qnas = res.data;
      })
      .catch(e => {
        alert("error : " + e);
      });
  },
  methods: {
    showDetail(qnaNum) {
      router.push({
        path: "/QnADetail/" + qnaNum
      });
    },
    search() {
      console.log(this.searchOption + "/" + this.searchTxt);
      axios
        .get("/searchQnA/" + this.searchOption + "/" + this.searchTxt)
        .then(res => {
          this.qnas = res.data;
        })
        .catch(e => {
          console.log(e);
        });
    }
  }
};
</script>

